import * as THREE from 'three';

/**
 * 
 */
export default class Plane extends THREE.Object3D {

  /**
   * 
   * @constructor
   */
  constructor() {
    super();

    // 
    const gridHelper = new THREE.GridHelper(20, 10, 0xFFFFFF, 0xFFFFFF);
    this.add(gridHelper);

    // Axis
    const axisHelper = new THREE.AxisHelper(5);
    this.add(axisHelper);
  }

}



